@import url(https://fonts.googleapis.com/css?family=Russo+One&display=swap&subset=cyrillic,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Odibee+Sans&display=swap);
*{margin:0;padding:0;font-family:'Russo One', sans-serif;color:rgba(0,0,0,0.842)}body{background-position:0 0;background-position:initial;background-repeat:repeat;background-size:auto auto;background-size:initial;min-height:100vh;position:relative;background:url(../../static/media/background.8bc907b7.jpg)}.app{width:70vw;overflow:hidden;max-width:800px;min-width:300px;padding:15px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-align:center;border-radius:20px;box-shadow:14px 20px 27px 17px rgba(0,0,0,0.54);background:radial-gradient(circle, #fff 0%, rgba(255,255,255,0.74) 100%)}.searchBox{width:100%}.searchBox form{width:-webkit-fill-available;display:flex}.searchBox button{font-size:20px;padding:10px 20px;border-radius:10px;background:rgba(255,0,0,0.397);border:3px solid rgba(0,0,0,0.26);cursor:pointer}.searchBox button:hover{background:rgba(255,0,0,0.534)}.searchBox .searchBar{width:-webkit-fill-available;background:rgba(255,255,255,0.212);border-radius:10px;border:3px solid rgba(0,0,0,0.26);color:#6d6d6d;font-size:20px;padding:10px 20px;-webkit-appearance:none;-moz-appearance:none;appearance:none;outline:none;text-align:center}.searchBox .searchBar::-webkit-input-placeholder{color:#000}.searchBox .searchBar:-ms-input-placeholder{color:#000}.searchBox .searchBar::-ms-input-placeholder{color:#000}.searchBox .searchBar::placeholder{color:#000}.searchBox .searchBar:hover{background:rgba(255,255,255,0.76)}.searchBox .searchBar:focus{background:#f5f5f5;color:#000}.searchBox .searchBar:focus::-webkit-input-placeholder{color:rgba(17,17,17,0.781)}.searchBox .searchBar:focus:-ms-input-placeholder{color:rgba(17,17,17,0.781)}.searchBox .searchBar:focus::-ms-input-placeholder{color:rgba(17,17,17,0.781)}.searchBox .searchBar:focus::placeholder{color:rgba(17,17,17,0.781)}.mainInfo{padding:15px;margin:25px 0 0 0;font-size:20px;display:none;-webkit-transition:opacity 500ms;transition:opacity 500ms}.mainInfo img{width:40px;height:22px;margin:0 15px}.mainInfo h2{display:inline-block}.mainInfo .temp{display:flex;justify-content:center;margin-top:50px}.mainInfo .temp i{color:#000;font-size:150px;margin:0 0 0 25px}.mainInfo .temp h1{font-size:150px;font-family:'Odibee Sans', cursive;color:#000}.mainInfo .additional{margin:20px 0}.mainInfo .additional h3{margin:10px 0;border-bottom:4px solid #d0d0d0}@media screen and (max-width: 700px){.app{width:85vw;height:auto}.app{min-width:250px}.searchBox form{display:block}.searchBox button{margin-top:5px;padding:5px 20px;width:100%}.mainInfo{padding:10px;margin:15px 0 0 0;font-size:15px}.mainInfo img{height:22px;margin:0 0 -2px 15px}.mainInfo .temp{display:block;margin:10px auto}.mainInfo .temp i{font-size:100px;margin:10px auto 0 auto}.mainInfo .temp h1{font-size:150px}}

